import { RequisitionList } from '../models/requisitionList';
import {
  Item,
  ConfiguredProduct,
  Link,
  Sample,
  BundleOption,
  ConfigurableOption,
  RawCustomizableOption,
  GiftCardOption,
} from '@/requisitionList/data/models/item';

// Interface for raw API response data for a requisition list
export interface RawRequisitionListData {
  name: string;
  description: string;
  uid: string;
  updated_at: string;
  items_count: number;
  items: {
    items: RawItemData[];
    page_info: {
      total_pages: number;
      current_page: number;
      page_size: number;
    };
  };
}

export function transformRequisitionList(
  data: RawRequisitionListData
): RequisitionList | null {
  if (!data) {
    return null;
  }

  return {
    uid: data.uid,
    name: data.name,
    description: data.description,
    updated_at: data.updated_at,
    items_count: data.items_count,
    items: transformItems(data.items?.items),
    page_info: data.items?.page_info,
  };
}

// Interface for raw item data
interface RawItemData {
  uid: string;
  product: {
    sku: string;
    stock_status?: string;
    only_x_left_in_stock?: number | null;
  };
  quantity: number;
  customizable_options?: RawCustomizableOption[];
  bundle_options?: BundleOption[];
  configurable_options?: ConfigurableOption[];
  configured_product?: ConfiguredProduct;
  links?: Link[];
  samples?: Sample[];
  gift_card_options?: GiftCardOption;
}

function transformItems(items: RawItemData[]): Item[] {
  if (!items?.length) return [];

  return items.map((item: RawItemData) => {
    const transformedItem = {
      uid: item.uid,
      sku: item.product?.sku,
      quantity: item.quantity,
      stock_status: (item.product?.stock_status || 'IN_STOCK') as
        | 'IN_STOCK'
        | 'OUT_OF_STOCK',
      only_x_left_in_stock: item.product?.only_x_left_in_stock ?? null,
      customizable_options: item.customizable_options
        ? item.customizable_options.map((option) => ({
            uid: option.customizable_option_uid,
            is_required: option.is_required,
            label: option.label,
            sort_order: option.sort_order,
            type: option.type,
            values: option.values.map(
              (value: RawCustomizableOption['values'][number]) => ({
                uid: value.customizable_option_value_uid,
                label: value.label,
                price: value.price,
                value: value.value,
              })
            ),
          }))
        : [],
      bundle_options: item.bundle_options || [],
      configurable_options: item.configurable_options
        ? item.configurable_options.map((option) => ({
            option_uid: option.configurable_product_option_uid,
            option_label: option.option_label,
            value_uid: option.configurable_product_option_value_uid,
            value_label: option.value_label,
          }))
        : [],
      samples: item.samples
        ? item.samples.map((sample) => ({
            url: sample.sample_url,
            sort_order: sample.sort_order,
            title: sample.title,
          }))
        : [],
      gift_card_options: item.gift_card_options || {},
    };

    if (item.configured_product?.name) {
      return {
        ...transformedItem,
        configured_product: item.configured_product,
      };
    }
    return transformedItem;
  });
}
