import { fetchGraphQl } from '@/requisitionList/api';
import { Item, ConfigurableOption } from '@/requisitionList/data/models/item';
import { REFINE_PRODUCT } from '@/requisitionList/api/refineProduct/graphql/refineProduct';

/**
 * Encodes option UID string into base64 string for refineProduct query
 * Example: 'configurable/93/13' -> 'Y29uZmlndXJhYmxlLzkzLzEz'
 */
const encodeOptionUids = (item: Item): string[] => {
  return item.configurable_options.map((option: ConfigurableOption) => {
    return btoa(
      `configurable/${atob(option.configurable_product_option_uid)}/${atob(
        option.configurable_product_option_value_uid
      )}`
    );
  });
};

/**
 * Enriches configurable products with variant data by calling the refineProduct query
 *
 * @param items - Array of requisition list items
 * @returns Promise resolving to array of items with configured_product populated for configurable items
 */
export const enrichConfigurableProducts = async (
  items: Item[]
): Promise<Item[]> => {
  return Promise.all(
    items.map(async (item: Item) => {
      // Skip non-configurable items or items with no options selected
      if (
        !item.product ||
        !item.configurable_options ||
        item.configurable_options.length === 0
      ) {
        return item;
      }

      const optionIds = encodeOptionUids(item);

      const { errors, data: refineData } = await fetchGraphQl(REFINE_PRODUCT, {
        variables: {
          optionIds,
          sku: item.product.sku,
        },
      });

      if (errors) {
        console.error('Failed to refine product:', errors);
        return item;
      }

      if (!refineData?.refineProduct) {
        return item;
      }

      return {
        ...item,
        configured_product: refineData.refineProduct,
      };
    })
  );
};
