import { transformRequisitionList } from '@/requisitionList/data/transforms';
import { fetchGraphQl } from '@/requisitionList/api';
import { enrichConfigurableProducts } from '@/requisitionList/api/refineProduct';
import { handleFetchError } from '@/requisitionList/lib/fetch-error';
import { RequisitionList } from '@/requisitionList/data/models/requisitionList';
import { UPDATE_REQUISITION_LIST_MUTATION } from '@/requisitionList/api/updateRequisitionList/graphql/updateRequisitionList';
import { events } from '@adobe-commerce/event-bus';

export const updateRequisitionList = async (
  requisitionListUid: string,
  name: string,
  description?: string,
  pageSize?: number,
  currentPage?: number
): Promise<RequisitionList | null> => {
  return fetchGraphQl(UPDATE_REQUISITION_LIST_MUTATION, {
    variables: {
      requisitionListUid,
      name,
      description,
      pageSize,
      currentPage,
    },
  }).then(async ({ errors, data }) => {
    if (errors) return handleFetchError(errors);

    if (!data?.updateRequisitionList?.requisition_list) {
      return null;
    }

    // Enrich configurable products with variant data if items are present
    if (data.updateRequisitionList.requisition_list.items?.items) {
      data.updateRequisitionList.requisition_list.items.items =
        await enrichConfigurableProducts(
          data.updateRequisitionList.requisition_list.items.items
        );
    }

    const payload = transformRequisitionList(
      data.updateRequisitionList.requisition_list
    );

    events.emit('requisitionList/data', payload);

    return payload;
  });
};
